





























































































































import { AppService } from "@/service/app.service";
import { CafeModel, RequestData } from "@/x-vue/services/interfaces";
import Vue from "vue";
import Component from "vue-class-component";
import UploadImage from "@/x-vue/components/file/UploadImage.vue";
@Component({
  components: { UploadImage },
})
export default class CafeAdminEdit extends Vue {
  cafe: CafeModel = {} as CafeModel;
  app = AppService.instance;

  async mounted(): Promise<void> {
    // console.log("CafeAdminEdit::mounted(); ");

    let options: RequestData = {
      domain: this.$route.params.id,
    };

    try {
      // console.log(options);
      this.cafe = await this.app.api.cafeGet(options);
      console.log(this.cafe);
    } catch (e) {
      this.app.error(e);
    }
  }

  async onSubmit(): Promise<void> {
    let options: RequestData = {
      idx: this.cafe.idx,
      title: this.cafe.title,
      description: this.cafe.description,
      subcategories: this.cafe.subcategories,
      app_name: this.cafe.app_name,
      app_background_color: this.cafe.app_background_color,
    };

    try {
      this.cafe = await this.app.api.cafeUpdate(options);
      console.log(this.cafe);
      this.app.api.alert("Cafe Update", "Cafe update success");
    } catch (e) {
      this.app.error(e);
    }
  }
}
